import React, { useEffect, useState } from 'react'
import axios from 'axios';
import Swal from 'sweetalert2';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import ProgressBar from 'react-bootstrap/ProgressBar';


const App = () => {


  // const [isChecked, setIsChecked] = useState(false);
  const [now, setNow] = useState(0)
  const [task, setTask] = useState();
  const [tasks, setTasks] = useState({
    title: "",
    description: "",
    completed: false
  });
  const [show, setShow] = useState(false);
  const [showe, setShowe] = useState(false);
  const [id, setId] = useState()

  useEffect(() => {
    conexion()
  }, []);

  const conexion = () => {
    axios.get('https://taskj.vercel.app/api/taskpend').then((res) => {
      setTask(res.data)
    })
  }

  const handleClose = () => {
    setShow(false)
    setShowe(false)
  };

  const deleted = (id) => {
    // await axios.delete(`https://needlecpd.herokuapp.com/api/delneedle/${id}`)
    Swal.fire({
      title: 'Estás seguro?',
      text: "¡No podrás revertir esto!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si, Borrar!'
    }).then(async (result) => {
      if (result.isConfirmed) {
        await axios.delete(`https://taskj.vercel.app/api/deltaskpend/${id}`)
          .then(res => {
            Swal.fire({
              icon: 'success',
              title: 'Your file has been deleted.',
              showConfirmButton: false,
              timer: 1200
            })
          })
        conexion()
      }
    })
  }

  const create = () => {
    setShow(true)
  }

  const guardar = async (e) => {
    e.preventDefault();
    // await axios.post(`http://localhost:4002/api/taskpend`, tasks)
    await axios.post(`https://taskj.vercel.app/api/taskpend`, tasks)
      .then(res => {
        Swal.fire({
          icon: 'success',
          title: 'correcto',
          showConfirmButton: false,
          timer: 1200
        })
        setShow(false)
        conexion()
      })
  }

  const handleChange = (e) => {
    // setIsChecked(!isChecked);
    setTasks({
      ...tasks,
      [e.target.name]: e.target.value,
    });
    // console.log(isChecked)
  };



  const showedit = async (id) => {
    setId(id)
    // await axios.get(`https://bakend.vercel.app/api/edit-needle/${id}`)
    await axios.get(`https://taskj.vercel.app/api/edit-taskpend/${id}`)
      .then(res => {
        console.log(res.data)
        setTasks(res.data)
      })
    setShowe(true)
  }

  const edit = async (e) => {
    e.preventDefault();
    await axios.put(`https://taskj.vercel.app/api/update-taskpend/${id}`, tasks)
      .then(res => {
        console.log(res);
        console.log(res.data);
        Swal.fire({
          icon: 'success',
          title: 'Actualizado',
          showConfirmButton: false,
          timer: 1200
        })
      })
    setShowe(false)
    conexion()
  }



  if (!task) return null;
  return (
    <div className='justify-content-center align-items-center' >
      <header>
        <header className="d-flex  justify-content-between px-3">
          <h2>Tasks ({task.length})</h2>
          <div className='w-50 mt-2 justify-content-center align-items-center'>
            <ProgressBar now={now} label={`${now}%`} />;
          </div>
          <button
            to="/create-task"
            className="btn btn-primary p-1 m-2"
            onClick={create}
          >
            Create Task
          </button>
        </header>
      </header>
      <div className="card-group justify-content-center">
        {task.map((task) => (
          <div key={task._id} className="m-2">

            <div className="card text-start text-white p-1 " style={{ minWidth: "18rem", maxWidth: "18rem", backgroundColor: "#252525" }}>
              <div className='m-1 '>
                <div className='d-flex justify-content-between mx-2 '>
                  <h5 className='text-center '>{task.title}</h5>
                  <div>
                    <button className=" btn btn-secondary btn-sm m-1" onClick={showedit.bind(this, task._id)} >
                      Edit
                    </button>
                    <button className=" btn btn-danger btn-sm" onClick={deleted.bind(this, task._id)}>
                      delete
                    </button>
                  </div>
                </div>
                <p className="font-italic m-0 px-2">{task.description}</p>
                <div className='text-secondary text-right'>
                  <input
                    type="checkbox"
                    name="completed"
                    // checked={task.completed === false ? false : true}
                    onChange={handleChange}
                  /> Completada
                  {/* <p className="card-text text-dark font-italic px-2">{task._id}</p> */}
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      <Modal show={show} onHide={handleClose} >
        <Modal.Body className='bg-dark '>
          <form className="bg-dark p-4 text-white">
            <h4>Task:</h4>

            <input
              type="text"
              name="title"
              onChange={handleChange}
              value={task.title}
              className=" w-100 p-2 rounded mb-3 bg-secondary text-white "
              placeholder="Write a title"

            />
            <h4>
              Description:
            </h4>
            <label className='w-100'>
              <textarea
                type="text"
                name="description"
                onChange={handleChange}
                value={task.description}
                className=" w-100 p-2 rounded bg-secondary text-white"
                placeholder="Write a description"

              />
            </label>
          </form>
          <div className='d-flex justify-content-end p-2 bg-dark '>
            <Button className='m-1' variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" className='m-1' onClick={guardar} >
              Save Changes
            </Button>
          </div>

        </Modal.Body>
      </Modal>

      <Modal show={showe} onHide={handleClose} >
        <Modal.Body className='bg-dark '>
          <form className="bg-dark p-4 text-white">
            <h4>Task:</h4>

            <input
              type="text"
              name="title"
              onChange={handleChange}
              value={tasks.title}
              className=" w-100 p-2 rounded mb-3 bg-secondary text-white "
              placeholder="Write a title"

            />
            <h4>
              Description:
            </h4>
            <label className='w-100'>
              <textarea
                type="text"
                name="description"
                onChange={handleChange}
                value={tasks.description}
                className=" w-100 p-2 rounded bg-secondary text-white"
                placeholder="Write a description"

              />
            </label>
          </form>
          <div className='d-flex justify-content-end p-2 bg-dark '>
            <Button className='m-1' variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" className='m-1' onClick={edit} >
              Actualizar
            </Button>
          </div>

        </Modal.Body>
      </Modal>

    </div>

  )
}

export default App